.head{
    width: 100%;
    position: relative;
    z-index: 1;
    margin:0 auto;
}
.header{
    display: flex;
    justify-content: space-between;
    padding:16px 84px;
    background-color: white;
}
.header-user-info{
    display: flex;
    justify-content: space-around;
    width: 200px;
    align-items: center;
}
.header-user-info-name{
    font-style: normal;
    font-weight: normal;
    font-size: 20px;
    line-height: 48px;
    letter-spacing: -0.15px;
    color: #616161;
}
.header-info{
    height: 400px;
    display: flex;
    align-items: flex-end;
    padding: 100px;
}
.header-info-tittle{
    font-style: normal;
    font-weight: bold;
    font-size: 64px;
    line-height: 80px;

    color: #FFFFFF;
}
.header-logo{
    width: 38px;
    height: 38px;
}
.header-shape{
    width: 200px;
    display: flex;
    justify-content: space-around;
    align-items: center;
}
.header-shaper{
    width: 48px;
    height: 48px;
    background-color: white;
    text-align: center;
    border: 1px solid #858383;
    border-radius: 30px;
    text-decoration: none;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
}